export function getXy(e, diffY = 44) {
  let type = e.type;
  let x = 0,
    y = 0;

  switch (type) {
    case "touchend":
      x = e.changedTouches[0].clientX
      y = e.changedTouches[0].clientY - diffY
      break;
    default:
      x = e.touches[0].clientX;
      y = e.touches[0].clientY - diffY;
      break;
  }
  return [x, y]
}

export function getBase64Image(src, cb) {
  var canvas = document.createElement("canvas");
  if (typeof src === "string") {
    var img = new Image();
    img.src = src
    img.onload = () => {
      canvas.width = img.width;
      canvas.height = img.height;
      var ctx = canvas.getContext("2d");
      ctx.drawImage(img, 0, 0, img.width, img.height);
      var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
      var dataURL = canvas.toDataURL("image/" + ext);
      cb(dataURL)
    }
  }
}

export function getRgbRatio(ctx, w, h) {
  let imgData = ctx.getImageData(0, 0, w, h);
  let count = 0;
  let r = 0;
  let g = 0;
  let b = 0;
  let data = imgData.data;
  for (var i = 0; i < data.length; i += 4) {
    if (data[i + 3] > 0) {
      count++;
      r += data[i];
      g += data[i + 1];
      b += data[i + 2]
    }
  }

  return {
    r: parseInt(r / count) ? parseInt(r / count) : 0,
    g: parseInt(g / count) ? parseInt(g / count) : 0,
    b: parseInt(b / count) ? parseInt(b / count) : 0
  }
}



