<template>
  <div id="app">
    <Tabbar ></Tabbar>
    <div id="page-content">
      <keep-alive>
       <router-view/>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import "./assets/font-awesome/css/font-awesome.min.css"
import Tabbar from "./components/Tabbar";
export default {
  name: "App",
  components: {
    Tabbar
  },
  watch: {
    '$route'(to, from){
      
      // console.log(to)
    }
  },
};
</script>
<style>

</style>

<style lang="less">

@import url("./assets/less/common");

html,
body {
  height: 100%;
  overflow: hidden;
  -webkit-overflow-scrolling: unset;
}
#app {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}
#page-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 9;
  overflow: auto;
  // padding-top: 44px;
}
.range-slider{
  width: 100% !important
}
.range-slider-inner{
  .range-slider-fill{
    background-color: #c8c6c6
  }
  .range-slider-rail{
    background-color: #e9e9e9
  }
  .range-slider-knob{
    border: 2px solid #e7e7e7;
    box-shadow: none
  }
}

.swiper-pagination-bullet-active {
    opacity: 1;
    background: #989898;
}
.swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets {
 bottom: 0
}

/* 弹框切换放大渐变 */
.opacity-enter-active,
.opacity-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.opacity-enter,
.opacity-leave-to {
  opacity: 0;
}

</style>



