<template>
  <transition name="opacity">
    <div class="myborad">
      <div class="item add" @touchend="add">
        <div class="w icon">
          <i class="fa fa-plus"></i>
        </div>
      </div>
      <div class="item" v-for="(item,index) in list" :key="index">
        <div class="w">
          <div class="img-wrapper">
            <img :src="item.img"   alt="" srcset="">
          </div>
          <p class="name">画板{{index+1}}</p>
          <p class="time">{{item.time}}</p>
        </div>
      </div>
    </div>
  </transition>
 
</template>

<script>
import {getCache, BOARD} from "../js/cache"
export default {
  data() {
    return {
      list:[]
    }
  },
  watch: {
    '$route'(to){
      console.log(to)
      this.list = getCache(BOARD)
    }
  },
  methods: {
    add(){
      this.$bus.$emit("drawingboard_cleardata")
      this.$router.go(-1)
    }
  },
  created() {
    this.list = getCache(BOARD)
    console.log(this.list)
  },
}
</script>

<style lang="less" scope>

.myborad{
  display: flex;
  flex-wrap: wrap;
  .item{
    flex: 0 0 50%;
    height: 2.5rem;
    text-align: center;
    padding: 15px;
    .w{
      box-shadow: 0px 5px 16px 4px rgba(161, 161, 161, 0.3);
      background-color: #fff;
      border-radius: 5px;
      height: 100%;
      display: flex;
      flex-direction: column;
      .img-wrapper{
        flex: 1;
        overflow: hidden;
      }
      .name,.time{
        text-align: left;
        color: #131313;
        font-size: 14px;
        line-height: 1.6em;
        padding: 0 15px;
      }
      .time{
        color: #dddddd
      }
      img{
        width: 100%
      }
      .fa{
        color: #e1e1e1;
        font-size: 42px
      }
      &.icon{
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
    
  }
}


/* 弹框切换放大渐变 */
.opacity-enter-active,
.opacity-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.opacity-enter,
.opacity-leave-to {
  opacity: 0;
}

</style>




