import Vue from 'vue'
import Router from 'vue-router'
import DrawingBoard from '@/page/DrawingBoard'
import MyBorad from '@/page/MyBoard'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'DrawingBoard',
      component: DrawingBoard
    },
    {
      path: '/myBoard',
      name: 'MyBorad',
      component: MyBorad
    }
  ]
})



