<template>
  
    <div class="drawingboard-page">
      <!-- 浮动icon -->
      <handle-ponit
        :onRevoke="onRevoke"
        :onReset="resetCanvas"
        :onSubmit="onSubmit"
        :onSave="onSave"
        :onMyBoard="onMyBoard"
      ></handle-ponit>
      <div class="draw-wrapper" ontouchmove="event.preventDefault()">
        <DragImg :imgData="currentDrawImg" :onDrawToCanvas="onDrawToCanvas"></DragImg>
        <canvas
          ref="cvs"
          @touchstart="cvsMoveStart"
          @touchmove="cvsMove"
          @touchend="cvsMoveEnd"
          style="width:100%; height:100%"
        ></canvas>
        <!-- <div class="drag-img" @touchstart="1" @touchmove="1" @touchend="1" v-show="currentDrawImg" >
        <img :src="currentDrawImg.imgSrc" alt="">
        </div>-->
      </div>
      <div class="ctr-wrapper">
        <ul class="icon-wrapper">
          <!-- 模块 -->
          <DrawCtr
            iconName="component"
            :currentTool="currentTool"
            :isOpen="isComponentOpen"
            :touchEvent="changeCtrState"
          >
            <div
              class="tools-wrapper"
              @touchend.stop
              @touchstart.stop
              v-show="currentTool == 'component' && isComponentOpen"
            >
              <ImageSwiper :onTouchImg="onTouchImg" :img="currentDrawImg.imgSrc"></ImageSwiper>
            </div>
          </DrawCtr>
          <!-- 铅笔 -->
          <DrawCtr iconName="pencil" :currentTool="currentTool" :isOpen="isPencilOpen">
            <div class="tools-wrapper" v-show="currentTool == 'pencil' && isPencilOpen">
              <Range min="5" style="margin-bottom:10px" max="15" step="0.5" v-model="pencilSize"></Range>
              <ColorSelect :onColorChange="getColor"></ColorSelect>
            </div>
          </DrawCtr>
          <!-- 橡皮檫 -->
          <DrawCtr iconName="eraser" :currentTool="currentTool" :isOpen="isEraserOpen">
            <div class="tools-wrapper" v-show="currentTool == 'eraser' && isEraserOpen">
              <Range min="10" max="30" step="1" v-model="eraserSize"></Range>
            </div>
          </DrawCtr>
        </ul>
      </div>
      <Loading></Loading>
    </div>

</template>

<script>
import Vue from "vue";
import DrawCtr from "../components/DrawCtr";
import HandlePonit from "../components/DrawHandle";
import Masker from "../components/Masker";
import ImageSwiper from "../components/ImageSwiper";
import ColorSelect from "../components/ColorSelect";
import Range from "vue-range-slider";
import Loading from "../components/loading";
import Bus from "../js/bus";
import v4 from "uuid";
import { TOOL_PENCIL, Pencil } from "../tools";
import { TOOL_ERASER, Eraser } from "../tools";
import { getXy, getBase64Image, getRgbRatio } from "../js/common";
import DragImg from "../components/DragImg";
import { socketUrl } from "../js/config";
import { BOARD, getCache, setCache } from "../js/cache";
import "vue-range-slider/dist/vue-range-slider.css";

export default {
  name: "DrawingBoard",
  data() {
    return {
      isComponentOpen: true,
      isPencilOpen: true,
      isEraserOpen: true,
      currentTool: "component",
      pencilSize: 5,
      eraserSize: 5,
      pencilColor: "rgb(0,0,0)",
      undoHistory: [],
      currentDrawImg: {},
      cacheImgs: {}
    };
  },
  methods: {
    changeCtrState: function() {
      console.log("name");
    },
    getColor(color) {
      this.pencilColor = color;
    },
    cvsMove(e) {
      this.isPencilOpen = false;
      this.isComponentOpen = false;
      this.isEraserOpen = false;
      switch (this.currentTool) {
        case "component":
          break;
        case TOOL_PENCIL:
          this.PencilTool.onMouseMove(...getXy(e, this.tabbarHeight));
          break;
        case TOOL_ERASER:
          this.EraserTool.onMouseMove(...getXy(e, this.tabbarHeight));
          break;
        default:
          break;
      }
    },
    cvsMoveEnd(e) {
      this.isPencilOpen = true;
      this.isComponentOpen = true;
      this.isEraserOpen = true;
      var items = null;
      switch (this.currentTool) {
        case "component":
          break;
        case "pencil":
          items = this.PencilTool.onMouseUp(...getXy(e, this.tabbarHeight));
          break;
        case "eraser":
          items = this.EraserTool.onMouseUp(...getXy(e, this.tabbarHeight));
          break;
        default:
          break;
      }
      if (items) {
        this.undoHistory.push(items);
      }
    },
    cvsMoveStart(e) {
      switch (this.currentTool) {
        case "component":
          break;
        case "pencil":
          this.PencilTool.onMouseDown(
            ...getXy(e, this.tabbarHeight),
            this.pencilColor,
            this.pencilSize
          );
          break;
        case "eraser":
          this.EraserTool.onMouseDown(
            ...getXy(e, this.tabbarHeight),
            this.eraserSize
          );
          break;
        default:
          break;
      }
    },
    onRevoke(e) {
      if (this.undoHistory.length > 0) {
        var last = this.undoHistory.pop();
        var map = {
          pencil: "PencilTool",
          eraser: "EraserTool"
        };
        this.resetCanvas();
        this.undoHistory.forEach(item => {
          if (item.type == "component") {
            this.renderImage(item);
          } else {
            var tool = map[item.tool];
            this[tool].draw(item);
          }
        });
      }
    },
    onSave() {
      if (this.undoHistory.length > 0) {
        let boradData = getCache(BOARD);

        if (boradData == null || boradData.constructor === Object) {
          boradData = [];
        }
        console.log(boradData);
        var img = this.$refs.cvs.toDataURL("image/png", 0.1);
        let obj = {
          img: img,
          time: new Date().Format("yyyy/MM/dd"),
          boradData: this.undoHistory
        };
        boradData.push(obj);
        var r = setCache(BOARD, boradData);
        console.log(r);
        this.$toast.center("保存成功");
      }
    },
    resetCanvas() {
      ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
    },
    onSubmit() {
      let result = {};
      var kinds = {
        tree: [],
        human: [],
        house: [],
        _window: []
      };
      this.undoHistory.map(item => {
        var type = item.componentType;
        if (type && type in kinds) {
          kinds[type].push(item);
        }
      });

      //树 宽/长
      let treeRatio = 0;
      let treeRatioArr = kinds.tree.map(item => {
        treeRatio += item.info.w / item.info.h;
        return item.info.w / item.info.h;
      });
      if (treeRatioArr.length > 0) {
        treeRatio = treeRatio / treeRatioArr.length;
      }

      //窗面积
      let _windowArea = 0;
      let _windowAreaArr = kinds._window.map(item => {
        let area = item.info.w * item.info.h;
        _windowArea += area;
        return area;
      });

      //房面积
      let houseArea = 0;
      let houseAreaArr = kinds.house.map(item => {
        let area = item.info.w * item.info.h;
        houseArea += area;
        return area;
      });

      //人面积
      let humanArea = 0;
      let humanAreaArr = kinds.human.map(item => {
        let area = item.info.w * item.info.h;
        humanArea += area;
        return area;
      });

      let houseWindowRatio =
        _windowArea && houseArea ? _windowArea / houseArea : 0;
      let humanRatio = humanArea
        ? humanArea / (this.canvasCssWidth * this.canvasCssHeight)
        : 0;
      let rgb = getRgbRatio(ctx, this.canvasWidth, this.canvasHeight);
      result = {
        treeRatio: treeRatio,
        houseWindow: houseWindowRatio,
        man: humanRatio,
        ...rgb
      };
      let params = JSON.stringify(result);
      try {
        if (this.socket.readyState === 1) {
          this.$bus.$emit("loading_isShow", true);
          console.log(params);
          this.socket.send(params);
          setTimeout(() => {
            this.$bus.$emit("loading_isShow", false);
          }, 1000);
        } else {
          this.$toast.center("数据提交失败");
        }
      } catch (e) {
        this.$toast.center("数据提交失败");
      }
    },
    onMyBoard() {
      this.$router.push("/myBoard");
    },
    onTouchImg(img) {
      getBase64Image(img.imgSrc, base64 => {
        img.imgSrc = base64;
        this.currentDrawImg = img;
        this.$bus.$emit("dragImg_initBoxSize");
      });
    },
    onDrawToCanvas(imgData) {
      this.handlePlaceImage({
        left: imgData.x,
        top: imgData.y,
        width: imgData.w,
        height: imgData.h
      }).then(res => {
        res.componentType = this.currentDrawImg.type
          ? this.currentDrawImg.type
          : "";
        this.undoHistory.push(res);
        this.currentDrawImg = {};
      });
    },
    imagetoCanvas(img, sx, sy, imgWidth, imgHeight, clipWidth, clipHeight) {
      let canvas = ctx;
      let cw = clipWidth ? clipWidth : img.width;
      let ch = clipHeight ? clipHeight : img.height;
      canvas.width = cw;
      canvas.height = ch;
      ctx.drawImage(img, sx, sy, clipWidth, clipHeight);
      return canvas;
    },
    renderImage({ mid, pos, imgData, info }) {
      let img = this.cacheImgs[mid];
      if (img) {
        img.width = info.w;
        ctx.drawImage(img, pos[0], pos[1], info.w, info.h);
      } else {
        img = new Image();
        img.src = imgData;
        this.cacheImgs[mid] = img;
        img.width = info.w;
        img.onload = () => {
          ctx.drawImage(img, pos[0], pos[1], info.w, info.h);
        };
      }
    },
    handlePlaceImage(place) {
      return new Promise((res, rej) => {
        const img = new Image();
        const mid = "img_" + v4();
        img.src = this.currentDrawImg.imgSrc;
        this.cacheImgs[mid] = img;
        img.onload = () => {
          ctx.drawImage(img, place.left, place.top, place.width, place.height);
          let posInfo = {
            x: place.left,
            y: place.top,
            w: place.width,
            h: place.height
          };
          posInfo.center = [
            posInfo.x + posInfo.w / 2,
            posInfo.y + posInfo.h / 2
          ];
          res({
            mid: mid,
            pos: [place.left, place.top],
            info: { w: posInfo.w, h: posInfo.h },
            imgData: this.currentDrawImg.imgSrc,
            posInfo,
            type: "component"
          });
        };
      });
    }
  },
  components: {
    DrawCtr,
    HandlePonit,
    Masker,
    ImageSwiper,
    ColorSelect,
    Range,
    DragImg,
    Loading
  },
  created() {
    Bus.initBus.call(this);
    this.socket = null;

    if (WebSocket) {
      this.socket = new WebSocket(socketUrl);
      this.socket.onopen = () => {
        this.$toast.center("socket连接成功");
      };
      this.socket.onmessage = msg => {
        console.log(msg.data);
      };
      this.socket.onerror = err => {
        this.$toast.center("socket连接失败");
      };
    }
  },
  mounted() {
    window.ctx = this.$refs.cvs.getContext("2d");
    this.PencilTool = Pencil(ctx);
    this.EraserTool = Eraser(ctx);
    let canvas = this.$refs.cvs;
    let dpr = window.devicePixelRatio;
    // 获取css的宽高
    let { width: cssWidth, height: cssHeight } = canvas.getBoundingClientRect();
    // 根据dpr，扩大canvas画布的像素，使1个canvas像素和1个物理像素相等
    canvas.width = dpr * cssWidth;
    canvas.height = dpr * cssHeight;
    this.canvasWidth = canvas.width;
    this.canvasHeight = canvas.height;
    this.canvasCssWidth = cssWidth;
    this.canvasCssHeight = cssHeight;
    // 由于画布扩大，canvas的坐标系也跟着扩大，如果按照原先的坐标系绘图内容会缩小
    // 所以需要将绘制比例放大
    ctx.scale(dpr, dpr);
    this.tabbarHeight = document.querySelector("#tabbar").offsetHeight;
    // console.log(document.querySelector("#tabbar").offsetHeight)

    this.$bus.$on("drawingboard_cleardata", ()=>{
      this.undoHistory = [];
      this.resetCanvas()
    })
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.drawingboard-page {
  height: 100%;
  display: flex;
  flex-direction: column;
  .ctr-wrapper {
    height: 98/2px;
    background-color: #fff;
    box-shadow: 0px -5px 16px 4px rgba(161, 161, 161, 0.1);
    z-index: 9;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 1001;
  }
  .draw-wrapper {
    flex: 1;
    background-color: #fcfcfc;
    position: relative;
    font-size: 0;
    .drag-img {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 999;
    }
  }
  .icon-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    img {
      width: 42/2px;
    }
  }
}
.tools-wrapper {
  position: fixed;
  left: 30px;
  right: 30px;
  // min-height: 200px;
  background-color: #ffffff;
  box-shadow: 0px 0px 16px 4px rgba(161, 161, 161, 0.1);
  border-radius: 5px;
  bottom: 64px;
  padding: 15px;
  padding-bottom: 10px;
}
</style>



