<template>
<transition name="opacity">
  <div class="loading" v-show="isShow">
    <div class="wrapper">
      <img src="../assets/images/Loginlogo.png" alt="" srcset="">
      提交中...
    </div>
  </div>
</transition>
  
</template>

<script>
export default {
  data() {
    return {
      isShow: false
    }
  },
  mounted() {
    this.$bus.$on("loading_isShow",(b)=>{
      console.log("loading_isShow")
      if(b){
        this.isShow = b
      }else{
        this.isShow = !this.isShow
      }
    })
  },
}
</script>

<style lang="less">
  .loading{
    text-align: center;
    position: fixed;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .5);
    z-index: 99999;;
    .wrapper{
      width: 1rem;
      color: #fff;
      margin: 0 auto;
      font-size: 12px;
      padding-top: 2rem;
      img{
        width: 100%
      }
    }
  }
  /* 弹框切换放大渐变 */
.opacity-enter-active,
.opacity-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.opacity-enter,
.opacity-leave-to {
  opacity: 0;
}

</style>




