<template>
  <div class="tabbar-wrapper" ref="tabbar" id="tabbar">
    <div class="back"  ><i v-show="path!='/'" @touchend="back" class="fa fa-angle-left fa-2x"></i></div>
      心心画板
    <div class="else"></div>
  </div>
</template>

<script>
export default {
  name: 'Tabbar',
  data () {
    return {
      path: "/"
    }
  },
  watch: {
    '$route'(to, from){
      this.path = to.path
      console.log(to, from)
    }
  },
  methods: {
    back(){
      this.$router.go(-1)
    }
  },
  mounted() {
    
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.tabbar-wrapper{
  height: 44px;
  line-height: 44px;
  text-align: center;
  box-shadow: 0px 5px 16px 4px rgba(161,161,161,0.1);
  font-weight: bold;
  z-index: 999999;
  font-size: 16px;
  // position: fixed;
  left: 0;
  top: 0;
  width: 100%
}
.back{
  float: left;
  height: 100%;
  width: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  // background-color: #000
}
.else{
  float: right;
  height: 100%;
  width: 44px;
  // background-color: #000
}
</style>



